body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background: rgb(248, 220, 168);
    width: 100%;
    height: 100%;
}

.App {
  color: black;
}

.header {
  background: rgb(61, 192, 188);
  color: white;
  padding: 5px;
  margin-bottom: 25px;
  text-align: center;
  font-size: large;
}

.search {
  text-align: center;
  padding: 10px;
}

input {
  width: 20%;
}

input {
  padding: 5px;
  border-radius: 5px;
}

/*button {background: rgb(123, 224, 212);}

button:hover {background: rgb(120, 240, 226);}
button:active {background: rgb(93, 169, 160);}*/

.films {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

h2 {
  font-size: 3vh;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.film {
  padding: 15px;
  text-align: center;
  height: 500px;
  width: 300px; 

}

figure {
  height: 400px;
  width: 250px;
  display: block;
  margin-left: auto;
  margin-right: auto 
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.error {
  color: red;
  font-weight: bold;
}